.certificate-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* font-size: calc(10px + 2vmin); */
    max-height: 100vh;
}


.certificate-img {
    margin-top: 10px;
    height: 65vh;
    filter: drop-shadow(0 0 0.5rem gray);
    /* margin-bottom: 5px; */
}

.certificate-btn{
    /* width: 150px; */
    height: 45px;
    border-radius: 10px;
    border: none;
    background-color: #5F7EFF;
    color: white;
    font-weight: bold;
    cursor: pointer;
    font-size: 16px;
}

.certificate-link  {
    height: 30px;
    
    padding: 0 10px;
    padding-top: 7px;
    border-radius: 10px;
    background-color: #5F7EFF;
    color: white;
    font-weight: bold;
    cursor: pointer;
    font-size: 16px;
    text-decoration: none;
}

.certificate-form{
    display: flex;
    align-items: center;
    margin-top: 30px;
}

.certificate-form-input {
    /* font-family: "Karla", sans-serif; */
    border-radius: 5px;
    border: 1px solid #D5D4D8;
    text-indent: 5px;
    /* height: 2rem; */
    /* font-size: 20px; */
    font-size: 1.2rem;
    max-width: 40vw;
    height: 30px;
    
}

@media screen and (min-width: 800px) { 

    .certificate-img {
        margin-top: 20px;
        height: 80vh;
    }
}